import { Grid, IconButton, Paper, Typography } from "@material-ui/core";
import React, { Component } from "react";
import "./../../MiniDrawer.css";
import "./AboutUs.css";

export default class AboutUs extends Component {
  render() {
    return (
      <div>
        <Typography paragraph>About Us</Typography>
        <Paper style={{ padding: "15px" }}>
          <Grid
            container
            direction="row"
            justify="flex-start"
            alignItems="center"
            spacing={3}
          >
            <Grid item>
              <img
                style={{ borderRadius: "50%" }}
                src="https://lh3.googleusercontent.com/a-/AAuE7mBI4JEaaw4_y19Hz5mbJhDOnn4wmVLknGEsRXM3=s96-c"
                alt="IMG"
              />
            </Grid>
            <Grid item>
              <Grid
                container
                direction="column"
                justify="center"
                alignItems="flex-start"
              >
                <Grid item>
                  <Typography variant="h6">Dharmesh Patel</Typography>
                </Grid>
                <Grid item>
                  <Typography variant="subtitle1" className="details">
                    The Creator
                  </Typography>
                  <Typography variant="subtitle1" className="details">
                    Toronto, CA
                  </Typography>
                  <IconButton href="https://www.linkedin.com/in/95dharmesh/">
                    <i className="fab fa-linkedin-in details"></i>
                  </IconButton>
                  <IconButton href="https://github.com/dharmesh95">
                    <i className="fab fa-github icons details"></i>
                  </IconButton>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </div>
    );
  }
}
