import { Typography } from "@material-ui/core";
import React, { Component } from "react";
import { connect } from "react-redux";
import { postData } from "../../actions/action";
import {
  FETCH_ALL_RECOS,
  FETCH_ALL_VOTES,
  FETCH_VOTES_BY_EMAIL
} from "../../constants/types.constants";
import {
  GET_ALL_RECOS_URL,
  GET_ALL_VOTES_BY_EMAIL_URL,
  GET_ALL_VOTES_URL,
  POST_VOTE_URL,
  createUrl
} from "../../constants/url.constants";
import {
  generatePopularFoodData,
  mergeRecommendationsAndVotes,
  voteFoodData
} from "../../helpers/food.helper";
import { isAccessible } from "../../helpers/visibility.helper";
import VoteModel from "../../models/VoteModel";
import PermissionDenied from "../common/PermissionDenied";
import SimpleExpansionPanel from "./SimpleExpansionPanel";

class Food extends Component {
  constructor(props) {
    super(props);
    this.state = {
      foodData: {},
      popularFoodData: {}
    };
    this.vote = this.vote.bind(this);
  }

  vote(recommendationID, points) {
    const { foodData } = this.state;
    const { profileObj, addVote } = this.props;
    this.setState({
      foodData: voteFoodData(recommendationID, foodData, points)
    });
    let vote = new VoteModel(
      recommendationID,
      profileObj.email,
      points,
      new Date()
    );
    addVote(vote);
  }

  componentDidMount() {
    const {
      fetchRecommendations,
      fetchVotes,
      fetchVotesByEmail,
      profileObj,
      week
    } = this.props;
    fetchRecommendations(week[0]);
    fetchVotes(week[0]);
    fetchVotesByEmail(profileObj.email, week[0]);
  }

  componentDidUpdate(prevProps) {
    const { recommendationData, voteDataByEmail, voteData } = this.props;
    if (
      recommendationData !== prevProps.recommendationData ||
      voteDataByEmail !== prevProps.voteDataByEmail ||
      voteData !== prevProps.voteData
    ) {
      this.setState({
        foodData: mergeRecommendationsAndVotes(
          recommendationData,
          voteDataByEmail
        ),
        popularFoodData: generatePopularFoodData(recommendationData, voteData)
      });
    }
  }

  render() {
    const { foodData, popularFoodData } = this.state;
    const { nextWeek, profileObj } = this.props;
    return isAccessible(profileObj, "food") ? (
      <div>
        <Typography paragraph>Food</Typography>
        <Typography style={{ fontSize: "14px" }} paragraph>
          For week {nextWeek.fromDate} - {nextWeek.lastDate}
        </Typography>
        <SimpleExpansionPanel
          foodData={foodData}
          popularFoodData={popularFoodData}
          vote={this.vote}
          {...this.props}
        />
      </div>
    ) : (
      <PermissionDenied />
    );
  }
}

Food.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {
    recommendationData: state.recommendationReducer.recommendationData,
    voteData: state.voteReducer.voteData,
    voteDataByEmail: state.voteReducer.voteDataByEmail
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchRecommendations: date =>
      dispatch(postData(createUrl(GET_ALL_RECOS_URL), date, FETCH_ALL_RECOS)),
    fetchVotes: date =>
      dispatch(postData(createUrl(GET_ALL_VOTES_URL), date, FETCH_ALL_VOTES)),
    fetchVotesByEmail: (email, date) =>
      dispatch(
        postData(
          createUrl(GET_ALL_VOTES_BY_EMAIL_URL),
          { email, date },
          FETCH_VOTES_BY_EMAIL
        )
      ),
    addVote: vote => dispatch(postData(createUrl(POST_VOTE_URL), vote))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Food);
