export const checkIfEmpty = obj => {
  if (obj && Object.keys(obj).length) {
    return false;
  }
  return true;
};

export function getWeek() {
  let curr = new Date();
  curr.setHours(0, 0, 0, 0);

  let week = [];
  for (let i = 1; i <= 7; i++) {
    let first = curr.getDate() - curr.getDay() + i;
    let day = new Date(curr.setDate(first));
    week.push(day);
  }
  return week;
}

export function getNextWeek() {
  let nextWeek = {};

  let curr = new Date();
  curr.setHours(0, 0, 0, 0);

  let date = curr.getDate() - curr.getDay() + 8;
  let day = new Date(curr.setDate(date));
  nextWeek.fromDate = day.toLocaleDateString();

  date = curr.getDate() - curr.getDay() + 7;
  day = new Date(curr.setDate(date));
  nextWeek.lastDate = day.toLocaleDateString();

  return nextWeek;
}
